@import "globals.scss";
.loader,
.loader:after {
  border-radius: 50%;
  width: 5rem;
  height: 5rem;
}
.loader {
  margin: 60px auto;
  font-size: 10px;
  position: relative;
  text-indent: -9999em;

  -webkit-transform: translateZ(0);
  -ms-transform: translateZ(0);
  transform: translateZ(0);
  -webkit-animation: load8 1.1s infinite linear;
  animation: load8 1.1s infinite linear;
}
@-webkit-keyframes load8 {
  0% {
    -webkit-transform: rotate(0deg);
    transform: rotate(0deg);
  }
  100% {
    -webkit-transform: rotate(360deg);
    transform: rotate(360deg);
  }
}
@keyframes load8 {
  0% {
    -webkit-transform: rotate(0deg);
    transform: rotate(0deg);
  }
  100% {
    -webkit-transform: rotate(360deg);
    transform: rotate(360deg);
  }
}

.loader-dark {
  border-top: 1.1em solid rgba($textGrey, 0.2);
  border-right: 1.1em solid rgba($textGrey, 0.2);
  border-bottom: 1.1em solid rgba($textGrey, 0.2);
  border-left: 1.1em solid $textHeading;
}

.loader-light {
  border-top: 1.1em solid rgba($grey, 0.2);
  border-right: 1.1em solid rgba($grey, 0.2);
  border-bottom: 1.1em solid rgba($grey, 0.2);
  border-left: 1.1em solid $darkGrey;
}
